.app-album {
    position: relative;
    height: 100%;
    overflow: hidden;
    user-select: none;
}


.app-album-banner-container {
    display: flex;
    height: 100%;
    transition: transform 0.5s ease-in-out;
}

/* 遮罩 */
.app-album::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1;
}


.app-album-banner {
    flex: 0 0 100%;
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

/* 作品展示区 文字区 */
.app-album-works-display-container {
    position: fixed;
    top: 50%;
    right: 5%;
    transform: translateY(-50%);
    width: 4rem;
    z-index: 9;
}

.app-album-works-display-title,
.app-album-works-display-text {
    color: var(--app-text-color);
}

.app-album-works-display-title {
    font-size: 0.9rem;
    letter-spacing: 0.05rem;
}

/* 分隔线 */
.app-album-line {
    margin: 0.1rem 0;
    width: 20%;
    border: 2px solid #ffffff;
    transition: all 1s ease-in-out;
}

.app-album-works-display-container:hover .app-album-line {
    width: 95%;
}

/* 文本 */
.app-album-works-display-text {
    margin-top: 0.8rem;
}

/* 点击进入 button */
.app-album-works-display-enter {
    margin-top: 0.2rem;
    width: 35%;
    color: #212121;
    font-size: 0.25rem;
    text-align: center;
    font-weight: 900;
    padding: 0.06rem;
    border-radius: 0.05rem;
    background: #ffffff;
    transition: transform 0.5s;
}

.app-album-works-display-enter:hover {
    transform: scale(1.02);
}













.app-album-main {
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}


/* 图片预览组件 */
.app-album-preview {
    margin-top: 6vh;
    width: 80%;
    box-sizing: border-box;
}

/* 懒加载图片组件 */
.lazy-image-component {
    position: relative;
    overflow: hidden;
    display: inline-block;
    width: 30%;
    margin: 1% 1.65%;
}

/* 图片占满标签 */
.lazy-image-component img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    border-radius: 1%;
    transition: transform, opacity, 0.5s ease-in-out;
}

.lazy-image-component:hover img {
    transform: scale(1.2);
}

/* Loading */
.lazy-image-loading {
    text-align: center;
}


/* 分页上下页 */
.app-pagination {
    display: flex;
    justify-content: space-around;
    width: 50%;
}

/* 上下页按钮 */
.page-link {
    /* 背景透明 */
    background: transparent;
    border: none;
    cursor: pointer;
    /* 聚焦时无边框 */
    outline: none;
    color: rgba(153, 153, 153, 0.767);
    font-size: 0.2rem;
    width: 0.4rem;
    height: 0.4rem;
    border-radius: 0.1rem;
    background: #11111177;
    transition: 0.2s;
}

.page-link:hover {
    color: #eff1f5;
    background: #2d2b2b54;
}


/* 预览覆盖层 */
.preview-overlay {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    backdrop-filter: blur(100px);
    box-sizing: border-box;
    z-index: 9;
}

/* 图片预览容器 */
.preview-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 85%;
    max-height: 85%;
    z-index: 10;
}

/* 关闭按钮 */
.preview-close {
    position: absolute;
    top: 1%;
    right: 1%;
    padding: 0.1rem;
    /* 背景透明 */
    background: transparent;
    border: none;
    /* 字体颜色 */
    color: rgba(153, 153, 153, 0.767);
    font-size: var(--font-size-16);
    font-weight: 700;
    transition: 0.2s;
    cursor: pointer;
    outline: none;
}

.preview-close:hover {
    color: #eff1f5;
}

@media all and (max-width: 768px) {}

@media all and (max-width: 576px) {

    /* 图片分类容器 */
    .app-album-classification-container {
        grid-template-columns: repeat(1, 1fr);

    }

    .app-album-preview {
        width: 95%;
    }

    .lazy-image-component {
        width: 100%;
        height: 100%;
        margin: 5% auto;
    }
}